import Link from 'next/link';
import posed from "react-pose";
import colors from '../colors.js';
import { Navbar, Nav, NavItem, NavDropdown, MenuItem } from 'react-bootstrap';

const Square = posed.div({
  idle: { scale: 1 },
  hovered: { scale: 1.05 }
});

const pages = [
  "/",
  "About",
  "Team",
  "Projects",
  "Contact",
]

const CSS = () => (
  <section>
    <head>
      <meta charset="utf-8" />
      <meta name="viewport" content="width=device-width, initial-scale=1" />
      <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/css/bootstrap.min.css" integrity="sha384-BVYiiSIFeK1dGmJRAkycuHAHRg32OmUcww7on3RYdg4Va+PmSTsz/K68vbdEjh4u" crossorigin="anonymous"/>
      <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/bulma/0.6.2/css/bulma.min.css" />
      <link rel="stylesheet" href="https://use.fontawesome.com/releases/v5.5.0/css/all.css" integrity="sha384-B4dIYHKNBt8Bc12p+WXckhzcICo0wtJAoU8YZTY5qE0Id1GSseTk6S+L3BlXeVIU" crossorigin="anonymous"/>
    </head>
  </section>
);

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      h1: false,
      h2: false,
      h3: false,
      h4: false,
      h5: false,
      isActive: false,
    }
  }

  toggleNav = () => {
    console.log(!this.state.isActive);
    this.setState(prevState => ({
      isActive: !prevState.isActive
    }))
  }

  render() {
    return(
    <div>
      <CSS/>
      <nav className="navbar is-info is-fixed-top" role="navigation" aria-label="main navigation" style={{backgroundColor: colors.b1, height: 80, borderRadius: 0}}>
       <div className="navbar-brand is-flex" style={{color: colors.b1, justifyContent: "space-between"}}>
         <a href="/" style={{padding:5}}>
           <Square
             pose={this.state.h1 ? "hovered" : "idle"}
             onMouseEnter={() => this.setState({ h1: true })}
             onMouseLeave={() => this.setState({ h1: false })}>
             <img
               src="/static/img/icon.png"
               alt="The MindBank"
               width="auto"
               height="45"/>
             </Square>
          </a>
         <div className="navbar-burger is-info" onClick={this.toggleNav} style={{color: "white"}}>
           <span></span>
           <span></span>
           <span></span>
         </div>
       </div>

      <div className={ this.state.isActive ? 'navbar-menu is-active' : 'navbar-menu'}>
         <div className="navbar-start"></div>
         <div className="navbar-end is-flex" style={{alignItems: "center", padding: 20}}>
           <Square
             pose={this.state.h2 ? "hovered" : "idle"}
             onMouseEnter={() => this.setState({ h2: true })}
             onMouseLeave={() => this.setState({ h2: false })}>
             <a href="/about" style={{textDecoration: "none", color: "white", padding: 10}}>
               <b>About Us</b>
             </a>
            </Square>
            <Square
              pose={this.state.h3 ? "hovered" : "idle"}
              onMouseEnter={() => this.setState({ h3: true })}
              onMouseLeave={() => this.setState({ h3: false })}>
               <a href="/team" style={{textDecoration: "none", color: "white", padding: 10}}>
                 <b>Team</b>
               </a>
            </Square>
            <Square
              pose={this.state.h4 ? "hovered" : "idle"}
              onMouseEnter={() => this.setState({ h4: true })}
              onMouseLeave={() => this.setState({ h4: false })}>
             <a href="/projects" style={{textDecoration: "none", color: "white", padding: 10}}>
               <b>Projects</b>
             </a>
           </Square>
           <Square
             pose={this.state.h5 ? "hovered" : "idle"}
             onMouseEnter={() => this.setState({ h5: true })}
             onMouseLeave={() => this.setState({ h5: false })}>
             <a href="/contact" style={{textDecoration: "none", color: "white", padding: 10}}>
               <b>Contact</b>
             </a>
          </Square>
         </div>
       </div>
      </nav>


    </div>
  );
}
}




export default Header;
